@import url('https://fonts.googleapis.com/css2?family=Assistant&family=Bebas+Neue&family=Josefin+Sans:wght@300;400&family=Lilita+One&family=Nunito+Sans:wght@200;300;400;600&family=Open+Sans:wght@300&family=Teko:wght@400;500&family=Varela+Round&display=swap');
.post{
    
    width: 330px;
    height: 34em !important;
    margin: 0% 1% 4% 2%;
    font-weight: bold; 
    border-style:groove;
    padding: 4%;
    border-radius: 10px;
    max-width: 100%;
  display: block;
  -webkit-transition: all 1s ease;
  -moz-transition: all 1s ease;
  -ms-transition: all 1s ease;
  -o-transition: all 1s ease;
  transition: all 1s ease;
}
.postLink{
    color: rgb(255, 255, 255);
    margin: 0% 1% 4% 4%;
    outline: none;
    
 }
 .postLink:hover{
   text-decoration: none;
   color: #fff;
 }

.postImage{
    width: 92%;
    height: 250px;
    object-fit: cover;
    cursor: pointer;
    margin: 5% 4% 3% 4%;
}
.postCats{
    font-family: 'Varela Round', sans-serif;
    text-align: center;
    color: rgba(184, 44, 16, 0.8);
    margin-top: 3%;
    cursor: pointer;
}
.Date{
    color:  black !important;
    font-family: 'Josefin Sans', sans-serif;
    text-align: center; 
    padding-top: 1rem;
}
.postTitle{
    color:  black !important;
    font-family: 'Josefin Sans', sans-serif;
    text-align: center; 
    margin-top: 3%;
    margin-bottom: 2%;
    cursor: pointer;
    font-size: larger;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
}
.postDesc{
    color:  black !important;
    font-family: 'Open Sans', sans-serif;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    
}

@media only screen and (max-width: 1090px) {
    .post{
        margin: 0% !important;
        width: 380px;
    }
}

@media only screen and (max-width: 580px) {
    .post{
        margin: 0% !important;
        width: 330px;
    }
}

@media only screen and (max-width: 480px) {
    .post{
        width: 300px;
        min-height: 28em !important;
        padding: 0% 2% 4% 2%;
        margin-top: 3%!important;
        border-color: gainsboro;
        border-style:dashed;
        border-width: 2px;
    }
.postDesc{
font-size:small;
color: grey !important;
margin-bottom: 1em;
padding-left: 1em;
padding-right: 1em; 
}
.Date{
    padding-top: 0.1rem !important;
}
    
    .post:hover{
        -webkit-transform: scale(1.0);
        -moz-transform: scale(1.0);
        -ms-transform: scale(1.0);
        -o-transform: scale(1.0);
        transform: scale(1.0);
        }
    
}

@media only screen and (max-width: 400px) {
    .post{
        width: 270px;
        border-style: none;
    }
}
