  .item {
    height: 500px;
    width: 100%;
    padding-top: 2%;
  }
  .item::after{
    display: block;
position: absolute;
  background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0) 0, #fff 100%);
  margin-top: -250px;
  height: 250px;
  width: 100%;
  content: '';
  }
  .carousel-caption h5{
    font-size: 210%;
  color: rgb(24, 21, 21);
  text-align: center;
  font-family: 'Catamaran', sans-serif;
  font-weight: bold;
  line-height: 160%;
  cursor: pointer;
  margin-bottom: 0%;
  text-shadow: 3px 3px 3px rgba(255, 255, 255, 1);
  }
  .carousel-caption h5 {
    font-size: x-large;}
  .ccaption{
    font-family: 'Open Sans', sans-serif;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    font-size: 130%;
    color: rgb(113, 108, 108);
    margin-top: 0%;
  }
  .item img {
    width: 100%;
    height: 100%!important;
    margin: 0;
    
  }
  
  .carousel-control {
    background : none!important;
  }
  .recentBlog h5{
    font-family: 'Bebas Neue', cursive;
    font-size: 250%;
    font-weight: bolder;
    color: rgba(184, 44, 16, 0.8);
    
    margin: 4% auto 1% auto;
  }
  .cate h5{
    font-family: 'Bebas Neue', cursive;
    font-size: 250%;
    font-weight: bolder;
    color: rgba(184, 44, 16, 0.8);
    
    margin: 3% auto 1% auto;
  }
 
  @import url("https://fonts.googleapis.com/css?family=Arbutus+Slab|Open+Sans&display=swap");
  .articles {
    color: #000000;
    margin: 0% 0%;
    font-family: Verdana, sans-serif;
    font-weight: bolder;
    padding-top: -1% ;
  }
  
  
  
  .childArticle p {
    position: absolute;
    bottom: 0;
    font-size: 14px;
    
  }
  
  .articles {
    width:100%  }
  
  .card4articles {
    cursor: pointer;
    position: relative;
    height: 0;
    
    padding-bottom: 50%;
    width: 50%; /* Set the width of each article (minus some margin) */
    margin: 8px; /* Adjust the margin between articles for spacing */
    --offset-multiplier: 4px;
    transition: transform 0.6s ease;
    --translate: 0;
    transform: translate(var(--translate), var(--translate));
  }

  .articles {
    display: flex;
    flex-wrap: wrap;
}

  .card4articles {
    width: 47%;
    box-sizing: border-box;
    
  }
  @media only screen and (max-width: 1400px) {
  .carousel-caption h5 {
    font-size: large;}}
  @media only screen and (max-width: 768px) {
    .item {
      flex-basis: 100%; /* 2 columns on smaller screens */
    }
    .card4articles{
      width: 100% !important;
    }
    .item::after{
      display: block;
  position: absolute;
    background-image: none;}
    .carousel-caption h5 {
      overflow: hidden;
      text-overflow: ellipsis ;
    }
  }
  
  @media only screen and (max-width: 480px) {
    .item {
      flex-basis: 100%; /* 1 column on even smaller screens */
      
    }
    /* .carousel1{
      padding-top: 3%;
    }
    .carousel-inner{
      padding-bottom: 18%;
    } */
    .recentBlog h5{
      font-size: 180%;
    }
    .cate h5{
      font-family: 'Bebas Neue', cursive;
      font-size: 180%;
      margin-top: 7%;
      margin-bottom: 1%;
    }
    .item::after{
    margin-top: -250px;
    height: 250px;
    width: 100%;
    content: '';
    }
    .carousel-caption h5 {
      font-size: 110%;
      position: relative;
      top: 0px;
    }
    
    .item{
      padding: 0 !important;
    margin-left: 0 !important;
      width: 100%;
    }
    .card4articles {
      flex: 1 1 100%; /* initially 3 columns */
      box-sizing: border-box;
      
    }
    .carousel-caption h6{
      font-size: small;
      margin-bottom: 0%;
      position: relative;
      top: 70px;
      color: rgba(173, 170, 170, 1);
      line-height: 120%;
    }
    .d-md-block{
      padding: 0%;
    }
    .carousel-indicators{
      visibility: hidden;
    }
    .childArticle h2{
      font-size: 120% !important;
      color: rgb(61, 60, 60);
      margin-top: 4%;
    }
    
  }

  
  
  .childArticle {
    position: absolute;
    width: 100%;
    height: 100%;
    padding: 3% ;
    box-sizing: border-box;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1), 0px -4px 8px rgba(255, 255, 255, 0.8);
    border-radius: 6px;
    
    z-index: 5;
  }
  .Economy{
    background-image: url("../images/economy.jpeg");
    background-size: cover;
  }
  .Technology{
    background-image: url("../images/technology.jpg");
    background-size: cover;
  }
  .Operations{
    background-image: url("../images/operations.jpg");
    background-size: cover;
  }
  .Human{
    background-image: url("../images/humanResource.jpg");
    background-size: cover;
  }
  .Strategy{
    background-image: url("../images/strategy.jpg");
    background-size: cover;
  }
  .Finance{
    background-image: url("../images/finance.png");
    background-size: cover;
  }
  .Marketing{
    background-image: url("../images/marketing.jpg");
    background-size: cover;
  }
  .Leadership{
    background-image: url("../images/leadership.jpg");
    background-size: cover;
  }
  .Philosophy{
    background-image: url("../images/philosophy.jpg");
    background-size: cover;
    
  } 
  .childArticle:nth-child(1) {
    --offset: 0;
    z-index: 4;
  }
  .childArticle:nth-child(2) {
    --offset: 1;
    z-index: 3;
  }
  .childArticle:nth-child(3) {
    --offset: 2;
    z-index: 2;
  }
  .childArticle:nth-child(4) {
    --offset: 3;
    z-index: 1;
  }
  .childArticle:nth-child(5) {
    --offset: 4;
    z-index: 0;
  }

  .childArticle {
    transition: 0.7s ease;
    filter: grayscale(100%);
  -webkit-filter: grayscale(100%);
  -moz-filter: grayscale(100%);
    }
    
    .childArticle:hover{
        filter: grayscale(0%);
        -webkit-filter: grayscale(0%);
        -moz-filter: grayscale(0%);
    transition: 0.7s ease;
    }


  .childArticle h2{
    font-size: 160%;
    overflow: hidden;
    text-overflow: ellipsis;
    font-weight: bold;

    
  }

  @media screen and (max-width: 768px) {
    
    .item {
      height: 250px; /* set a smaller height for smaller screens */
    }
  }
  @media screen and (max-width: 1000px) {
  .childArticle{
    filter: grayscale(0%);
    color: #000000;
}}
  @media screen and (max-width: 480px) {
    .item {
      height: 150px; /* set an even smaller height for even smaller screens */
    }
  }